body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  /* background-color: #282c34; */
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: header; */
  /* color: white; */
}

#app {
  text-align: center;
}

#maintext {
  height: 40vh;
  width: 50vw;
  padding: 1vw;
  margin:5vh;
}

table.center {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

td {
  width: 10vw;
}

td.left, th.left {
  text-align:left;
}

td.middle, th.middle {
  text-align: center;
}

footer {
  min-height: 10vh;
}
